'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Alert, AlertDescription } from '@/components/ui/alert'
import type { Article } from '@/app/knowledge/page'

interface ArticleFormProps {
  onSubmit: (article: Omit<Article, 'id' | 'authorId' | 'authorName' | 'authorRole' | 'createdAt' | 'likes' | 'views' | 'featured'>) => void
}

const categories = [
  'Crop Cultivation',
  'Pest & Disease Management',
  'Soil & Fertilization',
  'Irrigation & Water Management',
  'Organic Farming',
  'Farm Technology',
  'Market & Economics',
  'Government Policies',
  'Sustainable Agriculture'
]

export default function ArticleForm({ onSubmit }: ArticleFormProps) {
  const [formData, setFormData] = useState({
    title: '',
    summary: '',
    content: '',
    category: '',
    tags: '',
    readTime: ''
  })
  
  const [errors, setErrors] = useState<Record<string, string>>({})
  const [isSubmitting, setIsSubmitting] = useState(false)

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.title.trim()) {
      newErrors.title = 'Article title is required'
    } else if (formData.title.trim().length < 10) {
      newErrors.title = 'Title should be at least 10 characters long'
    }

    if (!formData.summary.trim()) {
      newErrors.summary = 'Article summary is required'
    } else if (formData.summary.trim().length < 50) {
      newErrors.summary = 'Summary should be at least 50 characters long'
    }

    if (!formData.content.trim()) {
      newErrors.content = 'Article content is required'
    } else if (formData.content.trim().length < 200) {
      newErrors.content = 'Content should be at least 200 characters long'
    }

    if (!formData.category) {
      newErrors.category = 'Category is required'
    }

    if (!formData.readTime || parseInt(formData.readTime) <= 0) {
      newErrors.readTime = 'Valid read time is required'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) {
      return
    }

    setIsSubmitting(true)

    try {
      // Process tags
      const tags = formData.tags
        .split(',')
        .map(tag => tag.trim().toLowerCase())
        .filter(tag => tag.length > 0)
        .slice(0, 8) // Limit to 8 tags

      const articleData = {
        title: formData.title.trim(),
        summary: formData.summary.trim(),
        content: formData.content.trim(),
        category: formData.category,
        readTime: parseInt(formData.readTime),
        tags
      }

      onSubmit(articleData)
      
      // Reset form
      setFormData({
        title: '',
        summary: '',
        content: '',
        category: '',
        tags: '',
        readTime: ''
      })
    } catch (error) {
      console.error('Error submitting article:', error)
      setErrors({ submit: 'Failed to create article. Please try again.' })
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }))
    }
  }

  // Estimate read time based on content length
  const estimateReadTime = (content: string) => {
    const wordsPerMinute = 200
    const wordCount = content.trim().split(/\s+/).length
    return Math.max(1, Math.ceil(wordCount / wordsPerMinute))
  }

  // Auto-update read time when content changes
  const handleContentChange = (value: string) => {
    handleInputChange('content', value)
    const estimatedTime = estimateReadTime(value)
    if (value.trim().length > 100) {
      setFormData(prev => ({ ...prev, readTime: estimatedTime.toString() }))
    }
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {errors.submit && (
        <Alert variant="destructive">
          <AlertDescription>{errors.submit}</AlertDescription>
        </Alert>
      )}

      {/* Article Title */}
      <div>
        <Label htmlFor="title">Article Title *</Label>
        <Input
          id="title"
          value={formData.title}
          onChange={(e) => handleInputChange('title', e.target.value)}
          placeholder="e.g., Complete Guide to Organic Pest Control in Vegetable Farming"
          className={errors.title ? 'border-red-500' : ''}
        />
        {errors.title && (
          <p className="text-sm text-red-500 mt-1">{errors.title}</p>
        )}
        <p className="text-sm text-muted-foreground mt-1">
          {formData.title.length}/150 characters
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Category */}
        <div>
          <Label htmlFor="category">Category *</Label>
          <Select value={formData.category} onValueChange={(value) => handleInputChange('category', value)}>
            <SelectTrigger className={errors.category ? 'border-red-500' : ''}>
              <SelectValue placeholder="Select a category" />
            </SelectTrigger>
            <SelectContent>
              {categories.map((category) => (
                <SelectItem key={category} value={category}>
                  {category}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
          {errors.category && (
            <p className="text-sm text-red-500 mt-1">{errors.category}</p>
          )}
        </div>

        {/* Read Time */}
        <div>
          <Label htmlFor="readTime">Estimated Read Time (minutes) *</Label>
          <Input
            id="readTime"
            type="number"
            min="1"
            max="60"
            value={formData.readTime}
            onChange={(e) => handleInputChange('readTime', e.target.value)}
            placeholder="5"
            className={errors.readTime ? 'border-red-500' : ''}
          />
          {errors.readTime && (
            <p className="text-sm text-red-500 mt-1">{errors.readTime}</p>
          )}
          <p className="text-sm text-muted-foreground mt-1">
            Auto-estimated based on content length
          </p>
        </div>
      </div>

      {/* Article Summary */}
      <div>
        <Label htmlFor="summary">Article Summary *</Label>
        <Textarea
          id="summary"
          value={formData.summary}
          onChange={(e) => handleInputChange('summary', e.target.value)}
          placeholder="Write a brief summary that describes what readers will learn from this article. This will be displayed in article previews."
          rows={3}
          className={errors.summary ? 'border-red-500' : ''}
        />
        {errors.summary && (
          <p className="text-sm text-red-500 mt-1">{errors.summary}</p>
        )}
        <p className="text-sm text-muted-foreground mt-1">
          {formData.summary.length}/300 characters
        </p>
      </div>

      {/* Article Content */}
      <div>
        <Label htmlFor="content">Article Content *</Label>
        <Textarea
          id="content"
          value={formData.content}
          onChange={(e) => handleContentChange(e.target.value)}
          placeholder="Write your comprehensive article content here. Include detailed explanations, step-by-step instructions, examples, and practical tips that will help fellow farmers. Use clear headings and organize your content logically."
          rows={12}
          className={errors.content ? 'border-red-500' : ''}
        />
        {errors.content && (
          <p className="text-sm text-red-500 mt-1">{errors.content}</p>
        )}
        <p className="text-sm text-muted-foreground mt-1">
          {formData.content.length} characters | ~{estimateReadTime(formData.content)} min read
        </p>
      </div>

      {/* Tags */}
      <div>
        <Label htmlFor="tags">Tags (Optional)</Label>
        <Input
          id="tags"
          value={formData.tags}
          onChange={(e) => handleInputChange('tags', e.target.value)}
          placeholder="e.g., organic, pest-control, vegetables, sustainable (separate with commas)"
        />
        <p className="text-sm text-muted-foreground mt-1">
          Add up to 8 relevant tags to help readers find your article. Separate tags with commas.
        </p>
      </div>

      {/* Writing Guidelines */}
      <div className="bg-muted/30 rounded-lg p-4">
        <h4 className="font-medium text-foreground mb-2">Article Writing Guidelines</h4>
        <ul className="text-sm text-muted-foreground space-y-1">
          <li>• Write clear, actionable content that provides real value to farmers</li>
          <li>• Use simple language and explain technical terms when necessary</li>
          <li>• Include practical examples and step-by-step instructions</li>
          <li>• Organize content with clear headings and logical flow</li>
          <li>• Cite sources and share personal experiences when relevant</li>
          <li>• Ensure accuracy of information, especially for technical advice</li>
        </ul>
      </div>

      {/* Submit Button */}
      <div className="flex justify-end space-x-4">
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? 'Publishing Article...' : 'Publish Article'}
        </Button>
      </div>
    </form>
  )
}
