'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { useUser } from '@/context/UserContext'
import type { Article } from '@/app/knowledge/page'

interface ArticleCardProps {
  article: Article
  onLike: (articleId: string) => void
  featured?: boolean
}

export default function ArticleCard({ article, onLike, featured = false }: ArticleCardProps) {
  const { user, isAuthenticated } = useUser()

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-IN', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getRoleColor = (role: string) => {
    switch (role) {
      case 'expert':
        return 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-200'
      case 'farmer':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200'
    }
  }

  const handleReadMore = () => {
    // In a real app, this would navigate to the full article page
    alert(`Opening full article: ${article.title}`)
  }

  const handleLike = () => {
    if (!isAuthenticated) {
      alert('Please login to like articles')
      return
    }
    onLike(article.id)
  }

  const handleBookmark = () => {
    if (!isAuthenticated) {
      alert('Please login to bookmark articles')
      return
    }
    alert(`Bookmarked: ${article.title}`)
  }

  return (
    <Card className={`h-full flex flex-col hover:shadow-lg transition-shadow duration-300 ${
      featured ? 'border-primary/50 bg-gradient-to-br from-primary/5 to-transparent' : ''
    }`}>
      <CardHeader className="pb-3">
        <div className="flex items-start justify-between mb-3">
          <div className="flex items-center space-x-2">
            <span className="text-sm font-medium text-foreground">{article.authorName}</span>
            <Badge className={`text-xs ${getRoleColor(article.authorRole)}`}>
              {article.authorRole}
            </Badge>
            {featured && (
              <Badge className="text-xs bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200">
                Featured
              </Badge>
            )}
          </div>
          <div className="text-xs text-muted-foreground">
            {formatDate(article.createdAt)}
          </div>
        </div>

        <div className="flex items-center justify-between mb-2">
          <Badge variant="outline" className="text-xs">
            {article.category}
          </Badge>
          <div className="flex items-center space-x-3 text-xs text-muted-foreground">
            <span>{article.readTime} min read</span>
            <span>{article.views} views</span>
          </div>
        </div>

        <CardTitle className={`${featured ? 'text-xl' : 'text-lg'} mb-2 hover:text-primary cursor-pointer line-clamp-2`}>
          {article.title}
        </CardTitle>

        <CardDescription className="text-sm leading-relaxed line-clamp-3">
          {article.summary}
        </CardDescription>
      </CardHeader>

      <CardContent className="flex-1 flex flex-col justify-between">
        {/* Tags */}
        {article.tags.length > 0 && (
          <div className="flex flex-wrap gap-1 mb-4">
            {article.tags.slice(0, 3).map((tag) => (
              <Badge key={tag} variant="secondary" className="text-xs">
                #{tag}
              </Badge>
            ))}
            {article.tags.length > 3 && (
              <Badge variant="secondary" className="text-xs">
                +{article.tags.length - 3}
              </Badge>
            )}
          </div>
        )}

        {/* Actions */}
        <div className="space-y-3">
          <Button
            onClick={handleReadMore}
            className="w-full"
            size="sm"
          >
            Read Full Article
          </Button>

          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <Button
                variant="ghost"
                size="sm"
                onClick={handleLike}
                className="text-muted-foreground hover:text-primary p-0"
              >
                👍 {article.likes}
              </Button>
              
              <Button
                variant="ghost"
                size="sm"
                onClick={handleBookmark}
                className="text-muted-foreground hover:text-primary p-0"
              >
                🔖 Save
              </Button>
            </div>

            <Button
              variant="ghost"
              size="sm"
              className="text-muted-foreground hover:text-primary p-0"
            >
              Share
            </Button>
          </div>
        </div>

        {/* User's own article indicator */}
        {user?.id === article.authorId && (
          <div className="mt-3 pt-3 border-t border-border">
            <Badge variant="outline" className="text-xs">
              Your Article
            </Badge>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
