'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { useUser } from '@/context/UserContext'
import ArticleCard from '@/components/knowledge/ArticleCard'
import ArticleForm from '@/components/knowledge/ArticleForm'
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

export interface Article {
  id: string
  title: string
  content: string
  summary: string
  category: string
  authorId: string
  authorName: string
  authorRole: string
  createdAt: string
  readTime: number
  likes: number
  views: number
  tags: string[]
  featured: boolean
}

const categories = [
  'All Categories',
  'Crop Cultivation',
  'Pest & Disease Management',
  'Soil & Fertilization',
  'Irrigation & Water Management',
  'Organic Farming',
  'Farm Technology',
  'Market & Economics',
  'Government Policies',
  'Sustainable Agriculture'
]

// Mock data for demonstration
const mockArticles: Article[] = [
  {
    id: '1',
    title: 'Complete Guide to Organic Pest Control in Vegetable Farming',
    content: 'Organic pest control is essential for sustainable farming practices. This comprehensive guide covers natural methods, beneficial insects, companion planting, and integrated pest management strategies that work effectively without harmful chemicals...',
    summary: 'Learn effective organic pest control methods for vegetable farming including natural remedies, beneficial insects, and IPM strategies.',
    category: 'Pest & Disease Management',
    authorId: 'expert1',
    authorName: 'Dr. Amit Patel',
    authorRole: 'expert',
    createdAt: '2024-01-15T10:00:00Z',
    readTime: 8,
    likes: 45,
    views: 1250,
    tags: ['organic', 'pest-control', 'vegetables', 'sustainable'],
    featured: true
  },
  {
    id: '2',
    title: 'Soil Health Assessment: Testing and Improvement Techniques',
    content: 'Healthy soil is the foundation of successful farming. This article explains how to assess soil health, interpret soil test results, and implement improvement strategies for better crop yields...',
    summary: 'Comprehensive guide on soil health testing, analysis, and improvement techniques for optimal crop production.',
    category: 'Soil & Fertilization',
    authorId: 'expert2',
    authorName: 'Dr. Priya Sharma',
    authorRole: 'expert',
    createdAt: '2024-01-14T14:30:00Z',
    readTime: 6,
    likes: 38,
    views: 980,
    tags: ['soil-health', 'testing', 'fertilization', 'crop-yield'],
    featured: true
  },
  {
    id: '3',
    title: 'Water-Efficient Irrigation Systems for Small Farms',
    content: 'Water conservation is crucial for sustainable agriculture. This guide explores various irrigation systems suitable for small farms, including drip irrigation, sprinkler systems, and smart irrigation technologies...',
    summary: 'Explore water-efficient irrigation solutions perfect for small-scale farming operations.',
    category: 'Irrigation & Water Management',
    authorId: 'expert3',
    authorName: 'Rajesh Kumar',
    authorRole: 'expert',
    createdAt: '2024-01-13T09:15:00Z',
    readTime: 7,
    likes: 32,
    views: 756,
    tags: ['irrigation', 'water-conservation', 'small-farms', 'technology'],
    featured: false
  },
  {
    id: '4',
    title: 'Understanding PM-KISAN and Other Farmer Welfare Schemes',
    content: 'Government schemes play a vital role in supporting farmers. This article provides detailed information about PM-KISAN, crop insurance, and other welfare schemes available to farmers...',
    summary: 'Complete overview of government schemes and welfare programs available for farmers in India.',
    category: 'Government Policies',
    authorId: 'expert4',
    authorName: 'Sunita Devi',
    authorRole: 'expert',
    createdAt: '2024-01-12T16:45:00Z',
    readTime: 5,
    likes: 28,
    views: 642,
    tags: ['government-schemes', 'pm-kisan', 'welfare', 'policies'],
    featured: false
  }
]

export default function KnowledgePage() {
  const { user, isAuthenticated } = useUser()
  const [articles, setArticles] = useState<Article[]>(mockArticles)
  const [filteredArticles, setFilteredArticles] = useState<Article[]>(mockArticles)
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('All Categories')
  const [isNewArticleOpen, setIsNewArticleOpen] = useState(false)

  // Filter articles based on search and category
  useEffect(() => {
    let filtered = articles

    if (searchTerm) {
      filtered = filtered.filter(article =>
        article.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        article.summary.toLowerCase().includes(searchTerm.toLowerCase()) ||
        article.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))
      )
    }

    if (selectedCategory !== 'All Categories') {
      filtered = filtered.filter(article => article.category === selectedCategory)
    }

    // Sort by featured first, then by creation date
    filtered.sort((a, b) => {
      if (a.featured && !b.featured) return -1
      if (!a.featured && b.featured) return 1
      return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
    })

    setFilteredArticles(filtered)
  }, [articles, searchTerm, selectedCategory])

  const handleNewArticle = (newArticle: Omit<Article, 'id' | 'authorId' | 'authorName' | 'authorRole' | 'createdAt' | 'likes' | 'views' | 'featured'>) => {
    if (!user) return

    const article: Article = {
      ...newArticle,
      id: Date.now().toString(),
      authorId: user.id,
      authorName: user.name,
      authorRole: user.role,
      createdAt: new Date().toISOString(),
      likes: 0,
      views: 0,
      featured: false
    }

    setArticles(prev => [article, ...prev])
    setIsNewArticleOpen(false)
  }

  const handleLike = (articleId: string) => {
    if (!isAuthenticated) {
      alert('Please login to like articles')
      return
    }

    setArticles(prev =>
      prev.map(article =>
        article.id === articleId
          ? { ...article, likes: article.likes + 1 }
          : article
      )
    )
  }

  const canCreateArticle = isAuthenticated && (user?.role === 'expert' || user?.role === 'farmer')

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <div className="bg-gradient-to-r from-purple-50 to-pink-50 dark:from-purple-950 dark:to-pink-950 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-4xl font-bold text-foreground mb-4">
              Knowledge Hub
            </h1>
            <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
              Access expert articles, tutorials, and agricultural best practices from experienced farmers and specialists
            </p>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Search and Filter Bar */}
        <div className="flex flex-col md:flex-row gap-4 mb-8">
          <div className="flex-1">
            <Input
              placeholder="Search articles, topics, or tags..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full"
            />
          </div>
          
          <Select value={selectedCategory} onValueChange={setSelectedCategory}>
            <SelectTrigger className="w-full md:w-48">
              <SelectValue placeholder="Select category" />
            </SelectTrigger>
            <SelectContent>
              {categories.map((category) => (
                <SelectItem key={category} value={category}>
                  {category}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          {canCreateArticle && (
            <Dialog open={isNewArticleOpen} onOpenChange={setIsNewArticleOpen}>
              <DialogTrigger asChild>
                <Button className="w-full md:w-auto">
                  Write Article
                </Button>
              </DialogTrigger>
              <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
                <DialogHeader>
                  <DialogTitle>Write New Article</DialogTitle>
                </DialogHeader>
                <ArticleForm onSubmit={handleNewArticle} />
              </DialogContent>
            </Dialog>
          )}
        </div>

        {/* Results Summary */}
        <div className="mb-6">
          <p className="text-muted-foreground">
            Showing {filteredArticles.length} of {articles.length} articles
            {selectedCategory !== 'All Categories' && ` in ${selectedCategory}`}
            {searchTerm && ` matching "${searchTerm}"`}
          </p>
        </div>

        {/* Featured Articles Section */}
        {!searchTerm && selectedCategory === 'All Categories' && (
          <div className="mb-12">
            <h2 className="text-2xl font-bold text-foreground mb-6">Featured Articles</h2>
            <div className="grid md:grid-cols-2 gap-6">
              {articles.filter(article => article.featured).slice(0, 2).map((article) => (
                <ArticleCard 
                  key={article.id} 
                  article={article} 
                  onLike={handleLike}
                  featured={true}
                />
              ))}
            </div>
          </div>
        )}

        {/* All Articles */}
        <div>
          {!searchTerm && selectedCategory === 'All Categories' && (
            <h2 className="text-2xl font-bold text-foreground mb-6">All Articles</h2>
          )}
          
          {filteredArticles.length > 0 ? (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredArticles.map((article) => (
                <ArticleCard 
                  key={article.id} 
                  article={article} 
                  onLike={handleLike}
                />
              ))}
            </div>
          ) : (
            <div className="text-center py-12">
              <div className="text-6xl mb-4">📚</div>
              <h3 className="text-xl font-semibold text-foreground mb-2">
                No articles found
              </h3>
              <p className="text-muted-foreground mb-6">
                {searchTerm || selectedCategory !== 'All Categories'
                  ? 'Try adjusting your search or filter criteria'
                  : 'Be the first to share your knowledge with the community'}
              </p>
              {canCreateArticle && (
                <Button onClick={() => setIsNewArticleOpen(true)}>
                  Write First Article
                </Button>
              )}
            </div>
          )}
        </div>

        {/* Call to Action for Non-experts */}
        {isAuthenticated && user?.role !== 'expert' && (
          <div className="mt-16 text-center bg-muted/30 rounded-lg p-8">
            <h3 className="text-2xl font-semibold text-foreground mb-4">
              Share Your Knowledge
            </h3>
            <p className="text-muted-foreground mb-6">
              Have farming experience or expertise? Share your knowledge by writing articles to help fellow farmers
            </p>
            {canCreateArticle && (
              <Button onClick={() => setIsNewArticleOpen(true)}>
                Write Your First Article
              </Button>
            )}
          </div>
        )}

        {/* Call to Action for Non-authenticated Users */}
        {!isAuthenticated && (
          <div className="mt-16 text-center bg-muted/30 rounded-lg p-8">
            <h3 className="text-2xl font-semibold text-foreground mb-4">
              Join Our Knowledge Community
            </h3>
            <p className="text-muted-foreground mb-6">
              Register to access exclusive content, bookmark articles, and contribute your own knowledge
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button asChild>
                <a href="/auth/register">Join Community</a>
              </Button>
              <Button variant="outline" asChild>
                <a href="/auth/login">Login</a>
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
